<!---->
<style lang='scss' rel="stylesheet/scss">
    @import "../../../utils/mixins/mixins";
    @import "../../../utils/mixins/table";
    @import "../../../utils/mixins/common";
    @import "../../../utils/mixins/topSearch";

    .block-manage {
        @extend %justify;
        .content-title {
            padding: 10px 20px;
            background: #f0f3f5;
            text-align: left;
            line-height: 35px;
            button {
                float: right;
                display: block;
                margin-right: 15px;
            }
        }
        .left-content {
            display: inline-block;
            vertical-align: top;
            width: 25%;
            background: #fff;

            .classify-tree {
                padding: 1px;
                .el-tree-node{
                    overflow: hidden;
                }
                p{
                    padding: 10px;
                    font-size: 12px;
                    color:red;
                }
            }
        }
        .right-content {
            width: 74%;
            display: inline-block;
            vertical-align: top;
            background: #fff;
            padding-bottom: 20px;
            section{
                margin-left: 10px;
                margin-bottom: 14px;
                display: inline-block;
                margin-right: 10px;
                .el-select .el-input .el-input__icon{
                    transform: translateY(-50%) translateX(-50%)rotateZ(180deg);
                }
            }
            .content-title{
                position: relative;
                section{
                    opacity:0.1;
                    height:0;
                    width:107px;
                    position: absolute;
                    top:10px;
                    right:23px;
                }
            }
            .content-list {
                padding: 20px;
                .search{
                    width:100%;
                    margin-bottom:10px;
                    section{
                        #input{
                            width: 84%;
                            display: inline-block;
                            margin-left: 10px;
                        }
                    }
                    .fi{
                        width:35%;
                    }
                }
                .tag {
                    span {
                        padding: 10px;
                        border: 1px solid #e2e7eb;
                        background: #fff;
                        border-right: none;
                        &:last-of-type {
                            border-right: 1px solid #e2e7eb;
                        }
                        &:hover {
                            background: #e2e7eb;
                        }
                        &.active {
                            background: #e2e7eb;
                        }
                    }
                }
            }
            .el-pagination {
                float: right;
                margin-top: 10px;
            }
        }
    }
</style>
<template>
    <article class="block-manage">
        <section class="left-content">
            <div class="content-title">
                
                <router-link tag="el-button" :to="{name: 'exam-questionbank-category'}"></router-link>
            </div>
            <div class="classify-tree">
                <p></p>
                 <MenuTree :data="SecMenu" v-if="SecMenu.length" :onNodeClick="treeNodeClick.bind(this,1)" ref="qustionbankCategory"></MenuTree>
            </div>
        </section>
        <section class="right-content">
            <div class="content-title">
                <span v-if="category.title">{{category.title}}-</span>
                <el-button type="primary" icon="plus" @click="addQuestion" ></el-button>
                <!-- <el-button type="danger" icon="plus"  @click="importQuestion" ></el-button> -->
            </div>
            <div class="content-list">
                <div class="search">
                    <section class="fi">
                     <i></i><el-input id="input" v-model="section.title" placeholder="" @keyup.enter.native="fetchCourseLists" auto-complete="off" ></el-input>
                    </section>    
                    <DateRange title="" :start="section.stime " :end="section.etime" @changeStart="val=> section.stime =val "
                        @changeEnd="val=> section.etime=val" :change="fetchCourseLists">
                    </DateRange>
                </div>     
                <el-table v-loading="section.loading" border :data="section.data">
                    <el-table-column prop="description" label="" width="180"></el-table-column>
                    <el-table-column prop="category_name" label="" >
                    </el-table-column>
                    <el-table-column prop="sort" label="" width="70"></el-table-column>
                    <el-table-column prop="addate" label="" width="200"></el-table-column>
                    <el-table-column prop="operate" label="" width="135" fixed="right">
                        <template scope="scope">
                            <el-button type="text" size="small" @click="update(scope.$index, scope.row)">
                                
                            </el-button>
                            <el-button type="text" size="small" @click="handleDelete(scope.$index, scope.row)">
                                
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="block">
                    <el-pagination
                            @current-change="sectionPageChange"
                            :current-page="section.page"
                            :page-size="section.pagesize"
                            small
                            layout="total,prev, pager, next"
                            :total="section.total">
                    </el-pagination>
                </div>
            </div>
        </section>
    </article>
</template>
<script>
    import libService from '../../../services/course/libService.js'
    import examService from '../../../services/exam/examService'    
    import courseService from '../../../services/course/courseService.js'
    import MenuTree from '../../component/tree/QustionsCategory.vue'
    import ImagEcropperInput from '../../component/upload/ImagEcropperInputSec.vue'
    import DateRange from '../../component/form/DateRangePicker.vue'
    import formUtils from '../../../utils/formUtils'
    function initSection() {
        return {
            loading: false,
            data: [],
            description:'',
            stime :'',
            etime:'',
            page: 1,
            pagesize: 15,
            total:0,
            // pid:0,
            status,
            title:'',
            deleted:0
        }
    }
    export default {
        name:'exam-questionbank-manage',
        components: {
            MenuTree,ImagEcropperInput
            ,DateRange
        },
        data () {
            return {
                ratio: 0, // 
                category: {
                    currentData: {},
                    loading: false,
                    title: '',
                    data: [],
                },
                // 
                formLabelWidth: '50px', // label
                editPlacehoder: '',
                rules: {
                    name: [
                        {
                            required: true,
                            message: '',
                            trigger: 'blur'
                        }
                    ]
                },
                section:initSection(),
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                SecMenu:[],
                category_id:void 0,
                Mult:'true',//  ,
                selectData:{},
                ended:void 0,
                disabled:true,
            }
        },
        watch: {
            '$store.state.index.secMenu'(){
                this.category.currentData = Object.assign({},this.$store.state.index.secMenu) //vuex 
            },
        },
        created () {
            // this.$refs.qustionbankCategory.getInitData();
            xmview.setContentLoading(false)     
            this.category.currentData.id = ''
            this.category.loading = true
            this.section=initSection()
            setTimeout(() => {
                this.fetchData()
                this.fetchCourseLists() 
            }, 300);
            
        },
         computed: {
            examCateid( ){
                return this.$store.state.index.examCate //Vue examCate
            }
        },
        methods: {
            
            // 
            treeNodeClick (type, data, node, store) {
                if (type == 1) { 
                    this.selectData = Object.assign({},node.data)  //
                    this.ended=node.data.ended
                    console.log('node.data.ended',node.data.ended);
                    if(this.selectData.id&&this.ended==1){
                        this.disabled=false
                    }else{
                        this.disabled=true
                    }
                    this.category.currentData.category_id=this.selectData.id
                    this.category.currentData.category_name=this.selectData.name
                    this.category.currentData.questionBank = 1
                    this.fetchCourseLists () 
                }
            },
            //   01
            offline(index, row) {
                let txt = row.status == 0 ? '' : ''
                let finalStatus = row.status == 0 ? 1 : 0
                xmview.showDialog(`${txt} <span style="color:red">${row.course_name}</span> ?`, () => {
                    libService.offlineCourse({
                        id: row.id,
                        status: finalStatus
                    }).then((ret) => {
                        row.status = finalStatus
                    })
                })
            },
            fetchData() { //
                let param={
                            page: 1,
                            pagesize: -1,
                            pid:0,
                        }
                libService.fetchLibCategory( param).then((ret) => {
                    this.SecMenu=ret
                    console.log(ret);
                    console.log('this.SecMenu',this.SecMenu);
                    xmview.setContentLoading(false)     
                })
            },
            fetchCourseLists () { // 
                this.section.loading = true
                this.section.category_id = this.category.currentData.category_id
                let param=Object.assign({},this.section)
                delete param.total
                delete param.data
                return libService.fetchSubjectLists(param).then((ret) => {
                    this.section.data = ret.data
                    this.section.loading = false
                    this.section.total = ret._exts.total
                    let total= ret._exts.total
                })
            },
            handleDelete (index, row) {
                xmview.showDialog(`<i style="color:red">${row.description}</i>`, () => {
                    libService.delLib(row.id).then(() => {
                        xmview.showTip('success', '')
                        this.section.data.splice(index, 1) //
                    }).catch((ret) => {
                        xmview.showTip('error', ret.message)
                    })
                })
            },
            importQuestion(){
                if(!this.selectData.id||!this.ended==1){
                    xmview.showTip('warning','')
                    return
                }
                this.$router.push({ name:'exam-subject-import',params:{chapterInfo:this.category.currentData}})
                console.log('this.category.currentData',this.category.currentData);
                
            },
            addQuestion(){
                if(!this.selectData.id||!this.ended==1){
                     xmview.showTip('warning','')
                     return
                }
                this.$router.push({ name:'course-manage-addLib',params:{addcourseInfo:this.category.currentData,handle:'add'}})
            },
            update (index, row) {
                console.log(row)
                this.$router.push({
                    name:'course-manage-addLib',
                    params: {courseInfo: row,handle:'edit', readonly:true},
                    query: {id: row.contentid},
                })
            },
            sectionPageChange (val) {
                this.section.page = val
                this.fetchCourseLists()
            }
        }
    }
</script>



